import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import CustomizationProduct from "../CustomizationProduct/CustomizationProduct";
import { faCircleNodes, faHandHoldingHeart, faHouseChimneyCrack, faPlus, faSitemap } from "@fortawesome/free-solid-svg-icons";
import { faPenToSquare } from "@fortawesome/free-regular-svg-icons";

const relatedCategories = ['إلكترونيات', 'موبايلات', 'آيفون', 'تخفيضات', 'إلكترونيات'];

const renderRelatedCategories = relatedCategories.map((categ, index) => {
    return <h3 key={index} className={`gray-text !underline !ms-7 mb-3 relative ${index !== 0 ? "line-4" : ""}`}>{categ}</h3>
});

const relationsBoxs = [
    { name: 'تخصيص المنتج', icon: faPenToSquare, type: 'icon', event: true },
    { name: 'التوفر في المعارض', icon: faHouseChimneyCrack, type: 'icon' },
    { name: 'وزن المنتج', hintMess: "0.45 أوقية", icon: faSitemap, type: 'text' },
    { name: 'جدول القياسات', icon: faCircleNodes, type: 'icon' },
    { name: 'أهدي من تحب', hintMess: 'يمكن الان ارسال الهدايا لمن تحب عن طريق منصتنا وفي الوقت الذي تحبه! ', icon: faHandHoldingHeart, btnText: "أرسل كهدية", type: 'btn' },
    { name: 'احصل عليه الان قبل الجميع', hintMess: 'يمكن الان ارسال الهدايا لمن تحب عن طريق منصتنا وفي الوقت الذي تحبه! ', btnText: "اطلبه الان", type: 'btn' },
];

const handelBoxType = (box, setCustomization) => {
    return box.type === 'icon' ?
        <div className='w-[30px] h-[30px] rounded-full border flex items-center justify-center cursor-pointer' onClick={_ => box.event ? setCustomization(true) : null}>

            <FontAwesomeIcon icon={faPlus} className='text-[var(--min-color)]'></FontAwesomeIcon>

        </div>
        : box.type === 'text' ?
            <p className='gray-text !text-black ms-2'>{box.hintMess}</p>
            :
            <button className='border border-[var(--min-color)] p-2 sm:px-5 text-[var(--min-color)] text-[11px] sm:text-[15px] font-normal rounded-[5px]'>{box.btnText}</button>

}

const boxStyle = (box, index, setCustomization) => {
    return <div key={index} className='border rounded-[5px] p-3 mb-3 flex items-center justify-center'>

        <div className='flex-1 flex items-center'>

            {box?.icon && <FontAwesomeIcon icon={box.icon} className='text-[var(--min-color)]' />}

            <div className='ms-2'>

                <p className='gray-text !text-black'>{box.name}</p>

                {box.hintMess && <h5 className='text-[var(--text-color)] text-[11px] sm:text-[12px]'>{box.hintMess}</h5>}

            </div>


        </div>

        {handelBoxType(box, setCustomization)}

    </div>
}

const renderRaltionsBoxs = (customization, setCustomization) => {
    return relationsBoxs.map((box, index) => {
        return (

            box.event ?
                customization ?
                    <CustomizationProduct customization={customization} setCustomization={setCustomization} />
                    :
                    boxStyle(box, index, setCustomization)
                :
                boxStyle(box, index, setCustomization)

        )
    });
}

export {
    renderRelatedCategories,
    renderRaltionsBoxs
}