import { features } from "./data"

const renderFeatures = features.map((feat, index) => {
    return (
        <div key={index} className={`grid grid-cols-12 gap-5 p-4 ${index % 2 !== 0 ? "bg-[#F8F8F8]" : ""}`}>

            <p className="col-span-3 gray-text">{feat.name}</p>
            <h5 className="col-span-8 font-medium">{feat.value}</h5>

        </div>
    )
})
export default function ProductFeatures() {
    return (
        <section>

            {renderFeatures}

        </section>
    )
}
