import React, { useState } from 'react'
import { renderSmImages } from './data';



export default function ProductImageOverview() {

    const [activeImage, setActiveImage] = useState('two');

    return (

        <div className='grid grid-cols-12 gap-5'>

            <div className='col-span-12 sm:col-span-4 flex items-center justify-between flex-row sm:flex-col order-1 sm:order-none mb-10 sm:mb-0'>

                {renderSmImages(activeImage, setActiveImage)}

            </div>
            <div className='col-span-12 sm:col-span-8 border p-5 rounded-[5px] flex flex-col items-center justify-center'>

                <img src={require(`../../assests/images/image-${activeImage}.png`)} className='max-h-[500px] min-h-[500px] sm:min-h-fit sm:max-h-[90%] object-contain h-full cursor-pointer' />

            </div>

        </div>

    )
}
