import React, { Fragment, useState } from 'react'
import { ProdcutOverview, ProductDetails, Sponsors, ProductFeatures, ProductRating } from './components';
import { SeactionHeader, SwiperWrapper } from '../../components';
import { items } from './data';

export default function ProductEnsemble() {

    const [barPrograse, setBarPrograse] = useState(0);

    return (
        <Fragment>

            <div className='container !my-10'>

                <div className='grid grid-cols-12 gap-5'>

                    <div className='col-span-12'>

                        <ProdcutOverview />

                    </div>

                    <div className='col-span-12 my-5'>

                        <Sponsors />

                    </div>

                    <div className='col-span-12 my-5'>

                        <div className='flex items-center border-b-2 border-b-[var(--min-color)]'>

                            <button onClick={() => setBarPrograse(0)} className={`font-medium text-[12px] sm:text-[16px] transition p-4 rounded-tr-[5px] rounded-tl-[5px] ${barPrograse === 0 ? "bg-[var(--min-color)] text-white" : "text-black"}`}>تفاصيل المنتج</button>
                            <button onClick={() => setBarPrograse(1)} className={`font-medium text-[12px] sm:text-[16px] transition p-4 ${barPrograse === 1 ? "bg-[var(--min-color)] text-white" : "text-black"}`}>مميزات المنتج</button>
                            <button onClick={() => setBarPrograse(2)} className={`font-medium text-[12px] sm:text-[16px] transition p-4 rounded-tl-[5px] ${barPrograse === 2 ? "bg-[var(--min-color)] text-white" : 'text-black'}`}>تقييمات المنتج</button>

                        </div>

                        <div className='border-l border-r border-b p-2 pt-5'>

                            {barPrograse === 0 && <ProductDetails />}
                            {barPrograse === 1 && <ProductFeatures />}
                            {barPrograse === 2 && <ProductRating />}

                        </div>

                        <div className='my-32'>

                            <div className='mb-5'>

                                <SeactionHeader href={''} title={'تسوق احدث المنتجات المميزة المضافة جديد'} paragraph={'منتجات مشابهة'} />

                            </div>
                            <SwiperWrapper
                                items={items}
                                includeNavigation={false}
                                slidesPerViewCount={[1.5, 3, 4, 4]}
                                autoplayDelay={3000}
                                isLooped={true}
                            ></SwiperWrapper>
                        </div>


                    </div>

                </div>

            </div>

        </Fragment >
    )
}
