const images = ['one', 'two', 'three', 'four', 'five'];

export const renderSmImages = (activeImage, setActiveImage) => {
    return images.map((image, index) => {
        return (
            <div key={index} onClick={_ => setActiveImage(image)} className={`border p-1 rounded-[5px] min-h-[120px] sm:!w-full transition flex items-center justify-center ${index !== images.length - 1 ? "sm:mb-4" : ""} ${activeImage !== image ? " opacity-50" : ""}`} style={{ width: "calc(93% / 5)" }}>

                <img src={require(`../../assests/images/image-${image}.png`)} className='max-h-[120px] min-h-[120px] object-contain cursor-pointer' />

            </div>
        )
    })
}