const colors = [
    { name: "أصفر", value: "#FFAF44" },
    { name: "أحمر", value: "#F55157" },
    { name: "أسود", value: "#1D1F1F" },
    { name: "أبيض", value: "#EEEEEE" },
    { name: "رمادي", value: "#EEEEEE" },
];

export const renderColors = colors.map((color, index) => {
    return (
        <div key={index}>

            <div className={`w-[30px] h-[30px] relative rounded-full me-7 after:top-2 mb-2 ${index > 0 && "line-4"}`} style={{ backgroundColor: color.value }}> </div>

            <p className='font-medium text-[#A5A5A5]'>{color.name}</p>

        </div>
    )
});

export const renderProductImages = [1, 2, 3, 4, 5].map(item => {
    return (
        <div key={item} style={{ width: "calc(93% / 5)" }}>

            <img src={require('./image.png')} className='mb-3' />

            <p className='font-medium text-[#A5A5A5] text-[11px] sm:text-[15px]'>عنوان الصوره</p>

        </div>
    )
})