import React, { useState } from 'react'
import { DropDown, Rating } from '../../../../components';
import { renderComments } from './data';

export default function ProductRating() {

    const [selectedItem, setSelectedItem] = useState({ name: "الاحدث", code: 1 });

    return (
        <section className='grid grid-cols-12 gap-5'>

            <div className='col-span-12 lg:col-span-4 h-fit border rounded-[5px] p-5'>

                <div className='p-5 bg-[#F8F8F8] text-center'>

                    <h2 className='font-medium mb-3'>4.5 من 5</h2>

                    <div className='flex items-center justify-center'>
                        <Rating rate={4} isDisabled={true} />
                    </div>

                    <p className='gray-text my-3'>4.5 من 5</p>

                </div>

                <h2 className='font-medium mb-2 mt-5'>أضف تعليقك</h2>

                <textarea rows={4} className='p-3 border mb-3 w-full gray-text resize-none rounded-[5px]' placeholder='برجاء اضافة تعليقك ..' ></textarea>

                <div className='my-3 flex items-center justify-center'>

                    <button className='font-medium p-3 w-full text-center bg-[var(--min-color)] rounded-[5px] text-white'>نشر تعليقك</button>

                </div>

            </div>

            <div className='col-span-12 lg:col-span-8'>

                <div className='flex items-center justify-between border rounded-[5px] p-2 py-5 sm:p-5 mb-10 overflow-hidden'>

                    <h2 className='font-medium text-[12px] sm:text-[15px] me-2'>تعليقات المستخدمين</h2>

                    <div className='flex items-center justify-between products-dropdown'>

                        <p className='gray-text min-w-[50%]'>ترتيب حسب : </p>

                        <DropDown selectedItem={selectedItem} setSelectedItem={setSelectedItem} items={[{ name: "الاحدث", code: 1 }, { name: "الاقدم", code: 1 }]} />

                    </div>

                </div>

                {renderComments}

                <div className='my-10 flex items-center justify-center'>

                    <button className='bg-transparent font-medium p-3 px-20 border border-[var(--min-color)] rounded-[5px] text-[var(--min-color)]'>عرض المزيد</button>

                </div>


            </div>

        </section>
    )
}
