import React from 'react'
import { data } from './data'

export default function Sponsors() {

    return (

        <section className='grid grid-cols-12 gap-5'>

            {
                data.map((item, index) => {
                    return (
                        <div key={index} className='col-span-12 lg:col-span-4 border rounded-[5px] p-4'>

                            <img src={require(`./images/sponser-${item.logo}.png`)} alt='' className='mb-5' />

                            <h6 className='font-medium mb-1 text-[var(--text-color)] text-[14px]'>{item.describe}</h6>

                            <h3 className='font-medium text-[var(--min-color)] text-[14px] cursor-pointer hover:!underline'>أعرف أكثر</h3>

                        </div>
                    )
                })
            }

        </section>

    )
}
